import React, { useState, useEffect } from 'react'
import Persons from './components/Persons'
import AddPerson from './components/AddPerson'
import Filter from './components/Filter';
import personService from './services/persons'
import Notification from './components/Notification'

const App = () => {
    const [persons, setPersons] = useState([])

    const [newPerson, setNewPerson] = useState({ name: '', number: '' })
    const [showAll, setShowAll] = useState(true)
    const [limit, setLimit] = useState('')
    const [message, setMessage] = useState(null)
    const [category, setCategory] = useState(null)

    useEffect(() => {
        personService
            .getAll()
            .then(response => {
                setPersons(response)
            })
    }, [])

    const addPerson = (event) => {
        event.preventDefault()

        const personObject = {
            name: newPerson.name,
            number: newPerson.number,
        }

        if (persons.map(person => person.name).includes(newPerson.name)) {
            setCategory('error')
            setMessage(`${newPerson.name} on jo luettelossa`)
            setTimeout(setMessage, 2000, null)
        }
    
        else {
            personService
                .create(personObject)
                .then(person => {
                    setPersons(persons.concat(person))
                    setNewPerson({ name: '', number: '' })
                    setLimit('')
                })
            setCategory('info')
            setMessage(`${newPerson.name} lisättiin onnistuneesti`)
            setTimeout(setMessage, 2000, null)
        }
    }
    const removePerson = id => {
        const removedPerson = persons.find(person => person.id === id)
        if (window.confirm(`Haluatko varmasti henkilön ${removedPerson.name} tiedot`, 'Peruuta')) {
            personService
                .remove(id)
                .then(() => {
                    setPersons(persons.filter(person => person.id !== id))
                })
                .catch(error => {
                   setCategory('error')
                   setMessage(`${removedPerson.name} on jo poistettu`)
                   setPersons(persons.filter(person => person.id !== id)) 
                })
        }

    }
    const handleNameChange = (event) => {
        setNewPerson({ number: newPerson.number, name: event.target.value })
    }
    const handleSearchCriteriaChanged = (event) => {
        if (event.target.value.length > 0) {
            setShowAll(false)
        }
        else {
            setShowAll(true)
        }
        setLimit(event.target.value)
    }
    const handleNumberChange = (event) => {
        setNewPerson({ number: event.target.value, name: newPerson.name })
    }
    return (
        <div>
            <div>
                <h2>Puhelinluettelo</h2>
                <Filter
                    limit={limit}
                    handleSearchCriteriaChanged={handleSearchCriteriaChanged}
                />
            </div>
            <Notification category={category} message={message} />
            <AddPerson
                addPerson={addPerson}
                handleNameChange={handleNameChange}
                handleNumberChange={handleNumberChange}
                newPerson={newPerson}
            />
            <h2>Numerot</h2>
            <Persons
                persons={persons}
                showAll={showAll}
                limit={limit}
                removePerson={removePerson} />
        </div >
    )

}

export default App
